.tariff-card__title {
  @include tariff-title;
  margin: 9px 0 29px 70px;
  font-weight: 400;

  @media (min-width: $width-tablet) {
    margin: 0;
    margin-bottom: 24px;
  }

  @media (min-width: $width-desktop) {
    margin-left: 162px;
    margin-bottom: 73px;
  }
}

.tariff-card__wrapper {
  background: url("../img/icons/sprite-decor.svg#cat-slim");
  z-index: 10;
}

.tariff-card {
  background-color: $dark-page-color;
  padding: 24px 30px 18px 20px;
  margin-bottom: 20px;
  position: relative;

  @media (min-width: $width-tablet) {
    padding: 77px 63px 57px 53px;
    margin-bottom: 30px;
  }

  @media (min-width: $width-desktop) {
    width: 570px;
    padding: 77px 61px 59px 52px;
    margin-bottom: 0;
  }

  &::before {
    content: "";
    display: inline-block;
    position: absolute;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: $main-page-color;

    // не вставляется svg из спрайта
    // background: url("../../../img/icons/sprite-decor.svg#cat-slim");

    @media (min-width: $width-tablet) {
      width: 200px;
      height: 200px;
      border-radius: 50%;
      right: 93px;
      top: 92px;
    }

    @media (min-width: $width-desktop) {
      width: 100px;
      height: 100px;
      top: 47px;
      left: 52px;
      right: auto;
    }
  }
}

