.tariff-card__description {
  @include index-main-text;
  padding-bottom: 23px;
  border-bottom: 1px solid #d9d9d9;
  margin: 0;

  @media (min-width: $width-tablet) {
    display: inline-block;
    width: 340px;
    border: none;
    padding: 0;
    margin-bottom: 52px;
  }

  @media (min-width: $width-tablet) {
    margin-bottom: 27px;
  }
}

.tariff-card__link {
  display: block;
  position: relative;
  color: $title-color;
  font-family: $secondary-font;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  margin-top: 19px;

  &:active {
    opacity: 0.3;
  }

  @media (min-width: $width-tablet) {
    margin: 0;
    font-size: 20px;
  }

  &::after {
    content: url("../img/icons/arrow.svg");
    position: absolute;
    margin-left: 15px;

    @media (min-width: $width-tablet) {
      margin-left: 25px;
    }
  }
}
